.card-container {
  background-color: white;
  width: 70%;
  height: auto;
  margin: auto;
  margin-bottom: 2rem;
  padding: 20px;
  border: 2px solid brown;
}
.card-container h3{
  color: brown;
}
.card-container p {
  padding: 10px;
}

.card-container p #confirmados {
  color: orange;
}
.card-container p #recuperados {
  color: green;
}
.card-container p #muertos {
  color: red;
}
