.searchBar {
  width: 100%;
  min-height: 10rem;
}
.searchBar .titulo {
  margin-top: 2rem;
  color: white;
  text-align: center;
}

.searchBar form {
  width: 100%;
  height: auto;
  text-align: center;
  padding-top: 40px;
  padding-bottom: 40px;
}
form {
  width: 100%;
  height: auto;
}
form input {
  display: block;
  width: 70%;
  margin: auto;
  height: 2.5rem;
  border: 2px solid brown;
  border-radius: 5%;
}
form h2 {
  color: white;
}
.paises-contenedor {
  width: 100%;
  height: auto;
}
.paises {
  width: 50%;
  height: auto;
  margin: auto;
  margin-top: 2%;
  background-color: brown;
  color: white;
}
